/* Reset atributtes */
* {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
html {
  font-size: 62.5%;
}
body {
  display: block;
}

div {
  width: 100px;
  height: 100px;
}

a {
  text-decoration: none;
}
button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}

#root {
  background-color: black;
  display: flex;
  flex-direction: column;
  font-size: 1.6rem;
  gap: 2rem; /*tamb 5*/
  height: 100vh;
  /* margin: auto; */
  max-width: 136.6rem;
  min-height: 100vh;
  min-height: 65rem;
  overflow-y: auto;
  padding: 1.6rem;
  width: 100%;
}
/* CAMBIAR HEIGHT, PARA QUE RESPONDA EN SAFARI */

.video {
  -o-object-fit: cover;
  object-fit: cover;
  bottom: 0;
  height: 100%;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  width: 100%;
  z-index: 0;
}

.header {
  display: block;
  position: relative;
  width: calc(100% - 5rem);
  z-index: 1;
}
.header__container {
  color: white;
  max-width: 37.6rem;
  text-align: start;
  padding: 0.2rem 0 0 1.1rem;
}
.header__info {
  display: inline;
  font-size: 2.4rem;
  font-weight: 100;
}

.main {
  display: table;
  width: 100%;
  height: max-content;
  /* height: 250px; */
  justify-content: center;
  /*margin: auto;*/ /*quito margin auto en esta versi√≥n*/
  min-height: 35vh;
  z-index: 1;
}
.main__container {
  display: table-cell;
  vertical-align: middle;
  /* height: 19rem; */
  /* height: 20vh; /*relative medida*/
  text-align: center;
  max-width: 29.6rem;
  width: 100%;
}

tr {
  display: inline-table;
}

.main__logo {
  -o-object-fit: contain;
  object-fit: contain;
  height: -moz-max-content;
  height: -webkit-max-content;
  height: max-content;
  pointer-events: none;
  width: 100%;
}

.footer {
  align-items: flex-end;
  display: flex;
  justify-content: center;
  padding: 2rem 0 1rem 0;
  z-index: 1;
}
.footer__icon {
  display: flex;
  font-size: 3.6rem;
}

.personal {
  display: flex;
  flex-direction: column;
  gap: 1.7rem; /*detail*/
}
.personal__item {
  background-color: transparent;
  color: white;
  display: block;
  font-size: 2.4rem;
  padding: 0.2rem;
  text-align: center;
  width: 100%;
}

.loading {
  align-items: center;
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  width: 11rem;
}
.loading__text::after {
  -webkit-animation: loadingText 1s linear infinite;
  animation: loadingText 1s linear infinite;
  color: white;
  content: "";
  font-size: 1.5rem;
  transition: 0.3s;
}
.loading__icon {
  -webkit-animation: loadingIcon 1s linear infinite;
  animation: loadingIcon 1s linear infinite;
  border: 0.2rem solid white;
  border-bottom-color: transparent;
  border-radius: 50%;
  color: white;
  display: block;
  height: 1.5rem;
  width: 1.5rem;
}
@-webkit-keyframes loadingText {
  0% {
    content: "loading";
  }
  25% {
    content: ".loading";
  }
  50% {
    content: "..loading";
  }
  100% {
    content: "...loading";
  }
}
@keyframes loadingText {
  0% {
    content: "loading";
  }
  25% {
    content: ".loading";
  }
  50% {
    content: "..loading";
  }
  100% {
    content: "...loading";
  }
}
@-webkit-keyframes loadingIcon {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes loadingIcon {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media (min-width: 904.98px) {
  #root {
    display: grid;
    gap: 0;
    grid-template-columns: 1fr 56rem 1fr;
    grid-template-rows: 1fr;
    padding: 6.4rem;
  }

  .header__container {
    left: 0;
    position: absolute;
    right: -9rem;
    text-align: start;
    top: 0;
    padding: 0;
  }
  .header__info {
    font-size: 2.4rem;
  }

  .main {
    display: flex;
    margin: auto;
    justify-self: inherit;
  }
  .main__container {
    display: block;
    max-width: 56rem;
    margin: auto;
  }

  .footer {
    justify-content: flex-end;
    padding: 0;
    position: relative;
    width: 100%;
  }
  .footer__container {
    bottom: 0;
    left: -8rem;
    position: absolute;
    right: 0;
  }

  .personal {
    gap: 1.2rem;
  }
  .personal__list {
    display: flex;
    justify-content: flex-end;
  }
  .personal__item {
    align-items: center;
    color: white;
    display: grid;
    font-size: 2.4rem;
    justify-content: flex-end;
    max-width: 18rem;
    overflow: hidden;
    padding: 0.2rem;
    transition: 0.3s;
  }
  .personal__item:hover {
    width: 100%;
  }

  .loading {
    justify-content: flex-end;
  }
}

