@supports (-webkit-overflow-scrolling: touch) {
  table,
  caption,
  tbody,
  tfoot,
  thead,
  tr,
  th,
  td,
  article,
  aside,
  canvas,
  details,
  embed,
  figure,
  figcaption,
  footer,
  header,
  hgroup,
  menu,
  nav,
  output,
  ruby,
  section,
  summary,
  time,
  mark,
  audio,
  video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
  }

  /*  */
  #root {
    height: 88vh;
  }
  .main {
    min-height: 25vh;
  }
  .footer {
    padding: 0rem 0 1rem 0;
  }
}
